.contact-us-container {
    display: flex;
    justify-content: center;
}

.title {
    margin-right: 50px;
    width: 50%;
}

.title h3 {
    /*font-size: 50px;*/
    margin-bottom: 20px;
}

.title p {
    font-size: 16px;
    font-weight: 400;
    text-transform: initial;
}

.contact-form {
    display: flex;
    flex-direction: column;
}

.contact-form label:not(:last-child) {
    margin-bottom: 20px;
}

.submit-button {
    background-color: var(--blue);
    border: none;
    outline: none;
    cursor: pointer;
    padding: 10px 0;
    border-radius: 5px;
    color: white;
}

@media screen and (max-width: 750px) {
    .contact-us-container {
        flex-direction: column;
        align-items: center;
    }

    .title {
        margin-right: 0;
        margin-bottom: 40px;
        width: 100%;
    }
}